import { API } from '../config';

const signin = (email) => {
  const data = {
    email,
    ce_id: localStorage.getItem('ce_id') || ''
  }
  return fetch(`${API}/signin`, {
    method: 'POST',
    headers: {
      Accept: 'application/json',
      'Content-Type': 'application/json',
    },
    body: JSON.stringify(data),
  })
    .then((response) => {
      return response.json();
    })
    .catch((err) => {
      console.log(err);
    });
}

