import React, { useEffect, useState } from 'react';
import { Box, Link, TextField, Typography } from '@material-ui/core';
import { ChevronLeft, ChevronRight, Close, Delete, Edit, Save, Share, ShoppingCart } from '@material-ui/icons'
import './quickview.css';

const QuickView = ({ card, close, share, save, remove, putPurchase, previous, next, editable }) => {
  const [logo, setLogo] = useState('');
  const [editMode, setEditMode] = useState(false);
  const [description, setDescription] = useState('');

  useEffect(() => {
    setLogo(card.photo || '')
  }, [card])

  const goPreviewClicked = (e) => {
    e.stopPropagation();
    previous();
  }
  
  const goNextClicked = (e) => {
    e.stopPropagation();
    next();
  }

  const editClicked = () => {
    setEditMode(true);
    setDescription(card.description);
  }

  const saveClicked = () => {
    setEditMode(false);
    card.description = description;
    save(card);
  }

  return (
    <Box className='quickview-container' onClick={close}>
      <Box className='quickview' onClick={(e) => e.stopPropagation()}>
        <Box className='top-nav'>
          {editable && (
            <>
              <Box className={'top-nav-item ' + (card.purchased ? 'active' : '')} onClick={putPurchase}><ShoppingCart /></Box>
              <Box className={'top-nav-item ' + (card.shared ? 'active' : '')} onClick={share}><Share /></Box>
              {!editMode && <Box className='top-nav-item' onClick={editClicked}><Edit /></Box>}
              {editMode && <Box className='top-nav-item' onClick={saveClicked}><Save /></Box>}
              <Box className='top-nav-item' onClick={remove}><Delete /></Box>
            </>
          )}
          <Box className='top-nav-item' onClick={close}><Close /></Box>
        </Box>
        <Box className='quickview-content'>
          <Box className='left-bar'>
            <Box className='logo-container'>
              <img src={logo} alt="img"/>
            </Box>
            <Box className='photo-container'>
              <Box className='quickview-photo' onClick={() => setLogo(card.photo)}>
                <img src={card.photo} alt="img"/>
              </Box>
              {card.photos.map((photo, idx) => (
                <Box className='quickview-photo' key={idx} onClick={() => setLogo(photo)}>
                  <img src={photo} alt="img"/>
                </Box>
              ))}
            </Box>
          </Box>
          <Box className='right-bar'>
            <Typography variant="h5" gutterBottom>{card.name}</Typography>
            <Typography variant="h4" gutterBottom style={{color: '#66ceff'}}>${card.price}</Typography>
            {!editMode && <Typography style={{whiteSpace: 'break-spaces'}}>{card.description}</Typography>}
            {editMode && <TextField
              className='description-editor'
              label="Description"
              multiline
              rows={8}
              value={description}
              onChange={(e) => setDescription(e.target.value)}
              variant="outlined"
            />}
            <Typography className='quickview-item-link'><Link href={card.url} target='_blank'>{card.url}</Link></Typography>
          </Box>
        </Box>
      </Box>
      <Box className='quickview-left-navigator' onClick={goPreviewClicked}><ChevronLeft fontSize='large'/></Box>
      <Box className='quickview-right-navigator' onClick={goNextClicked}><ChevronRight fontSize='large' /></Box>
    </Box>
  );
};

export default QuickView;
